import React from 'react';
import { useState, useEffect } from 'react';
import { collection, query, where, onSnapshot, deleteDoc, doc } from 'firebase/firestore';
import { db } from '../../firebase';
import QRGenerator from '../QRGenerator/QRGenerator';
import './Dashboard.css';

const Dashboard = ({ user }) => {
  const [qrCodes, setQrCodes] = useState([]);
  const [showGenerator, setShowGenerator] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    if (user) {
      const q = query(
        collection(db, 'qrcodes'),
        where('userId', '==', user.uid)
      );
      
      const unsubscribe = onSnapshot(q, (snapshot) => {
        const codes = snapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));

        // Sort by createdAt descending (newest first) on client side
        const sortedCodes = codes.sort((a, b) => {
          if (!a.createdAt || !b.createdAt) return 0;
          return b.createdAt.toDate() - a.createdAt.toDate();
        });

        setQrCodes(sortedCodes);
        setLoading(false);
        setError('');
      }, (err) => {
        console.error('Error fetching QR codes:', err);
        setLoading(false);
        if (err.code === 'permission-denied') {
          setError('Permission denied. Please check your Firestore security rules.');
        } else {
          setError('Failed to load QR codes. Please try refreshing the page.');
        }
      });

      return () => unsubscribe();
    }
  }, [user]);



  const handleDeleteQR = async (qrId) => {
    try {
      await deleteDoc(doc(db, 'qrcodes', qrId));
    } catch (error) {
      console.error('Error deleting QR code:', error);
    }
  };

  const downloadQR = (qrDataUrl, name) => {
    const link = document.createElement('a');
    link.download = `${name || 'qr-code'}.png`;
    link.href = qrDataUrl;
    link.click();
  };

  

  if (loading) {
    return <div className="loading">Loading your QR codes...</div>;
  }

  if (error) {
    return (
      <div className="dashboard">
        <main className="dashboard-main">
          <div className="error-state">
            <div className="error-icon"></div>
            <h3>Unable to load QR codes</h3>
            <p>{error}</p>
            <button
              onClick={() => window.location.reload()}
              className="retry-btn"
            >
              Retry
            </button>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="dashboard">
      <main className="dashboard-main">
        <div className="dashboard-actions" style={{ gap: 12 }}>
          <button 
            onClick={() => setShowGenerator(true)} 
            className="generate-btn"
          >
            + Generate New QR Code
          </button>
        </div>

        <div className={`qr-grid ${qrCodes.length === 1 ? 'single-item' : qrCodes.length === 2 ? 'two-items' : ''}`}>
          {qrCodes.length === 0 ? (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h3>No QR codes yet</h3>
              <p>Create your first QR code to get started!</p>
              <button 
                onClick={() => setShowGenerator(true)} 
                className="generate-btn"
              >
                Generate QR Code
              </button>
            </div>
          ) : (
            qrCodes.map((qr) => (
              <div key={qr.id} className="qr-card">
                <div className="qr-image">
                  <img src={qr.qrDataUrl} alt={qr.name} />
                </div>
                <div className="qr-info">
                  <h4>{qr.name}</h4>
                  <p className="qr-content">{qr.content}</p>
                  <p className="qr-date">
                    {new Date(qr.createdAt?.toDate()).toLocaleDateString()}
                  </p>
                </div>
                <div className="qr-actions">
                  <button 
                    onClick={() => downloadQR(qr.qrDataUrl, qr.name)}
                    className="download-btn"
                  >
                    Download
                  </button>
                  <button 
                    onClick={() => handleDeleteQR(qr.id)}
                    className="delete-btn"
                  >
                    Delete
                  </button>
                </div>
              </div>
            ))
          )}
        </div>
      </main>

      {showGenerator && (
        <QRGenerator 
          user={user} 
          onClose={() => setShowGenerator(false)} 
        />
      )}
    </div>
  );
};

export default Dashboard;