import { useState, useEffect } from 'react';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { db } from '../../firebase';
import QRCode from 'qrcode';
import './QRGenerator.css';

const QRGenerator = ({ user, onClose }) => {
  const [name, setName] = useState('');
  const [content, setContent] = useState('');
  const [qrDataUrl, setQrDataUrl] = useState('');
  const [loading, setLoading] = useState(false);
  const [isClosing, setIsClosing] = useState(false);

  // Prevent body scroll when modal is open
  useEffect(() => {
    document.body.style.overflow = 'hidden';
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, []);

  const generateQR = async () => {
    if (!content.trim() || !name.trim()) return;

    setLoading(true);
    try {
      const dataUrl = await QRCode.toDataURL(content, {
        width: 300,
        margin: 2,
        color: {
          dark: '#000000',
          light: '#FFFFFF'
        }
      });
      setQrDataUrl(dataUrl);

      // Save only if user is logged in
      if (user && user.uid) {
        await addDoc(collection(db, 'qrcodes'), {
          userId: user.uid,
          name: name.trim(),
          content: content.trim(),
          qrDataUrl: dataUrl,
          createdAt: serverTimestamp()
        });
      }

    } catch (error) {
      console.error('Error generating or saving QR code:', error);
    }
    setLoading(false);
  };



  const handleClose = () => {
    setIsClosing(true);
    setTimeout(() => {
      onClose();
    }, 300); // Match the animation duration
  };

  const downloadQR = () => {
    if (!qrDataUrl) return;

    const link = document.createElement('a');
    link.download = `${name || 'qr-code'}.png`;
    link.href = qrDataUrl;
    link.click();
  };

  return (
    <div className="qr-generator-overlay" onClick={handleClose}>
      <div className={`qr-generator-modal ${isClosing ? 'closing' : ''}`} onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Generate QR Code</h2>
          <button onClick={handleClose} className="close-btn"></button>
        </div>
        
        <div className="modal-content">
          <div className="form-section">
            <div className="form-group">
              <label>QR Code Name</label>
              <input
                type="text"
                placeholder="Enter a name for your QR code"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            
            <div className="form-group">
              <label>Content (URL or Text)</label>
              <textarea
                placeholder="Enter URL or text to encode"
                value={content}
                onChange={(e) => setContent(e.target.value)}
                rows={3}
              />
            </div>
            
            <button
              onClick={generateQR}
              className="generate-btn"
              disabled={!content.trim() || !name.trim() || loading}
            >
              {loading ? (user ? 'Generating & Saving...' : 'Generating...') : (user ? 'Generate & Save QR Code' : 'Generate QR Code')}
            </button>
          </div>
          
          {qrDataUrl && (
            <div className="qr-preview-section">
              <div className="qr-preview">
                <img src={qrDataUrl} alt="Generated QR Code" />
                <p className="qr-name">{name || 'Untitled QR Code'}</p>
              </div>
              
              <div className="qr-actions">
                <button
                  onClick={downloadQR}
                  className="download-btn"
                >
                  Download QR Code
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default QRGenerator;